.charts-page-sider .header {
  padding: 20px 20px 0px;
}
.charts-page-sider .header .icon-list {
  margin-top: 20px;
  font-size: 14px;
  padding-left: 5px;
}
.charts-page-sider .header .icon-list li:first-child {
  margin-bottom: 10px;
}
.charts-page-sider .header .icon-list i {
  color: #f6bb42;
  margin-right: 10px;
}
.charts-page-sider .charts-type-list .ant-list-item {
  padding-left: 28px;
  color: #999;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}
.charts-page-sider .charts-type-list .ant-list-item:hover {
  background-color: #eeeeee;
  color: #666;
}
.charts-page-sider .charts-type-list .ant-list-item .ant-list-item-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.charts-page-sider .charts-type-list .ant-list-item .ant-list-item-content > i {
  margin-right: 10px;
}
.charts-page > .ant-layout-content {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 24px;
}
